@import '../../style/App.scss';

#about {
  padding: 60px 60px 0 0;
  .desktopSkills {
    display: block;
  }
  .mobileSkills {
    display: none;
  }
}


@media screen and (max-width: 480px) { // Mobile
  #about {
    padding: 30px 0 0 0;
    p{
      font-size: 1.5rem;
    }
    .desktopSkills {
      display: none;
    }
    .mobileSkills {
      display: block;
    }
  }
}