@import '../../style/App.scss';

@media screen and (min-width: 768px) { // Desktop
  .hero {
    padding-top: 150px;
    width: 100%;
    padding-bottom: 40px;
    position: relative;
    .hero-info {
      position: absolute;
      top: 50%;
      bottom: 50%;
      .font-info {
        font-weight: 500 !important;
        font-size: 5.5rem;
        line-height:60px;
      }
      p {
        padding-top: 20px;
        padding-bottom: 50px;
      }
    }

    .hero-image {
      max-width: 570px;
      height: auto;
      float: right;
    }
  }
}
@media screen and (max-width: 768px) { // Mobile
  .hero {
    padding-top: 120px;
    width: 100%;

    .hero-info {
      padding-top: 120px;
      .font-info {
        font-weight: 800 !important;
        font-size: 3.5rem;
        line-height:40px;
      }
      p {
        padding-top: 5px;
        padding-bottom: 30px;
      }
    }

    .hero-image {
      float: none;
      width: 200px;
      height: 200px;
      margin: 0 auto;
    }
  }
}