@import '../../../style/App.scss';

.button {

  .button-link {
    background-color: $main-color;
    border: 2px solid $main-color;
    padding: 15px 30px;
    margin: 0 5px;
    cursor: pointer;
    font-size: 1.4rem;
    font-weight: 800;
  }
  @media screen and (max-width: 786px) { // Mobile and small tab
    .button-link {
      font-size: 1rem;
    }
  }
  .button-link:hover {
    border: 2px solid $main-color;
    background-color: transparent;
  }
}