@import '../../../style/App.scss';

.big__title {
  width: 100%;
  text-align: center;
  h2 {
    padding: 4rem 0;
  }
}

@media screen and (max-width: 990px) {
  .big__title {
    width: 100%;
    text-align: center;
    h2 {
      padding: 3rem 0;
    }
  }
}


@media screen and (max-width: 480px) {
  .big__title {
    h2 {
      font-size: 3.5rem;
      padding: 2rem 0;
    }
  }
}