@import '../../style/App.scss';


#partners {
  background-color: $light-color;
  padding: 70px 0 0;
}

@media screen and (max-width: 480px) { // Mobile
  #partners {
    padding: 60px 0 0 0;
  }  
}

@media screen and (min-width: 768px) and (max-width: 768px) { // Tablet
  #partners {
    padding: 130px 0 0 0;
  }  
}